import React, {useState, useEffect} from 'react';
import axios from 'axios';

const GetCode = (props) => {
    const {updateCodeTime} = props
    const [timeDate, setTimeDate] = useState({
        time: 60,
        time_txt: '',
        isYzmClick: false
    });

    useEffect(() => {
        if (timeDate.isYzmClick) {
            axios.post(props.data.codeUrl, props.data.postData).then(
                (res) => {
                    console.log(res)
                    let timer = setInterval(() => {
                        if (timeDate.time > 0) {
                            let time = timeDate.time--;
                            setTimeDate({
                                ...timeDate,
                                time_txt: time + '',
                                time: time,
                                isYzmClick: false
                            });
                            updateCodeTime({
                                ...props.data,
                                timeout: true
                            });
                        } else {
                            setTimeDate({
                                ...timeDate,
                                time_txt: '',
                                time: '60',
                                isYzmClick: false
                            });
                            updateCodeTime({
                                ...props.data,
                                timeout: false
                            });
                            clearInterval(timer)
                        }
                    }, 1000)
                },
                (error) => {
                    console.log('')
                    console.log(error)
                }
            )
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [timeDate.isYzmClick])

    return (
        <span onClick={() => {
            setTimeDate({
                ...timeDate,
                isYzmClick: true
            });
        }}>{timeDate.time_txt}</span>
    )
}

export default GetCode;