import React from 'react';

const QrPop = (props) => {

    const {updateShowHide} = props

    let txt;

    if (props.data.checkin_status === 0)
        txt = '<br><p class="bip"><br>15</p>'
    else if (props.data.checkin_status === 1)
        txt = '' + props.data.event_start_date + '<br/>' + props.data.event_end_date + '<br/><br/><p class="bip"><br/>10<br/><br/>1</p>'
    else
        txt = '' + props.data.event_start_date + '<br/>' + props.data.event_end_date + '<br/><br/><p class="bip">10<br/></p><p class="bipErr"></p>'

    return (
        <div className={'FollowPop'}>
            <div className={'box'}>
                <img alt={''} src={props.data.checkin_qrcode}/>
                <div className="content_txt" dangerouslySetInnerHTML={{__html: txt}}/>
                <div className={'btnBox'}>
                    <button className="close" onClick={() => {
                        updateShowHide(false)
                    }}>
                    </button>
                </div>
            </div>
            <div className={'desk'} onClick={() => {
                updateShowHide(false)
            }}/>
        </div>
    )
}

export default QrPop;