import React, {useState, useEffect} from "react";
import axios from "axios";

import {getUrlData} from "../util/getUrlData";

import l from "../assets/images/record/l.png";
import l1 from "../assets/images/record/l (1).png";
import gou from "../assets/images/record/gou.png";
import gou1 from "../assets/images/record/gou1.png";
import loading_gif from "../assets/images/circle-transparent.gif";

const Historical = (props) => {
    const [data, setData] = useState(
        {
            isAj: !!getUrlData('jordan', props.location.search),
            state: 6,
            btnYes: "",
            btnNo: "",
            btnboolean: true,
            isYzmClick: true,
            testing: "",
            histirucalRecord: "",
            store_id: parseInt(getUrlData('store_id', props.location.search)),
            fans_id: "",
            mobile: "",
            message: "",
            dataList: [{type_name:'',title:'',booking_time:'2020-12-20'}],
        });

    const [time, setTime] = useState({
        testing_txt: "",
        count: 61
    })

    if(data.isAj){
        window.document.body.style.backgroundColor = '#000';
    }

    useEffect(() => {
        axios.post(
            '/fans/delete-history',
            {
                store_id: data.store_id
            }).then(res => {
            let resData = res.data;
            resData.data.state = 3;
            resData.data.btnYes = '';
            resData.data.btnboolean = true;
            if (Number(resData.code) === 10001) {
                // 
                resData.data.state = 2;
                resData.data.btnboolean = false;
            }
            if (Number(resData.code) === 10002) {
                // 
                resData.data.state = 1;
                resData.data.dataList = resData.data;
                resData.data.btnboolean = false;
            }
            if (Number(resData.code) === 10003) {
                // 
                resData.data.state = 0;
                resData.data.btnboolean = false;
            }
            setData({...data, ...resData.data});
        });
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [data.store_id]);

    function getCode() {
        if (data.isYzmClick) {
            axios.post(
                '/fans/send-code',
                {
                    store_id: data.store_id
                }).then(res => {
                let resData = res.data;
                if (Number(resData.code) === 200) {
                    setTime({...time, testing_txt: '60'});
                    let timer = setInterval(() => {
                        if (time.count > 0) {
                            time.count--
                            setTime({...time, testing_txt: time.count + '', count: time.count});
                        } else {
                            setTime({...time, testing_txt: '', count: 61})
                            setData({...data, isYzmClick: true});
                            clearInterval(timer)
                        }
                    }, 1000)
                }else{
                    alert(resData.message);
                }
                data.isYzmClick = true;
            }, error => {
                setData({...data, isYzmClick: true});
                console.log(error)
            })
        }
        setData({...data, isYzmClick: false});
    }

    function btnClick() {
        if (data.state === 3) {
            setData({...data, state: 4, btnYes: ''})
        } else if (data.state === 4) {
            console.log(data.testing.length)
            if (data.testing.length === 6 && time.count > 0 && time.count < 61) {
                axios({
                    url: "/fans/delete-history-apply",
                    method: "post",
                    data: {
                        store_id: data.store_id,
                        fans_id: data.fans_id,
                        code: data.testing,
                    },
                    transformRequest: [
                        function(oldData){
                            // console.log(oldData)
                            let newStr = ''
                            for (let item in oldData){
                                newStr += encodeURIComponent(item) + '=' + encodeURIComponent(oldData[item]) + '&'
                            }
                            newStr = newStr.slice(0, -1)
                            return newStr
                        }
                    ],
                    headers: {
                        "Content-Type": "application/x-www-form-urlencoded",
                    },
                }).then(res => {
                    let resData = res.data;
                    if (Number(resData.code) === 200) {
                        setData({...data, state: 5, btnYes: '', btnboolean: false})
                    }
                    if (Number(resData.code) === 300) {
                        alert("");
                    }
                    console.log(res.data);
                })
            }
        }
    }

    return (
        <div className={data.isAj ? 'Historical jordan': 'Historical'}>
            <div className="section">
                {data.state !== 6 ?
                    <div className="histirucal-img">
                        <img className="img" alt={''} src={
                            data.isAj ?
                                data.state !== 5 ? l1 : gou1
                                :
                                data.state !== 5 ? l : gou
                        }/>
                    </div> : null
                }
                {data.state === 0 ?
                    <h1></h1> :
                    data.state === 1 ?
                        <h1></h1> :
                        data.state === 2 ?
                            <h1></h1> :
                            data.state === 3 || data.state === 4 ?
                                <h1></h1> :
                                data.state === 5 ?
                                    <h1></h1> : null
                }
            </div>
            {data.state === 0 ?
                <div className="histirucal-state0 p-d-t"></div> : null
            }
            {data.state === 3 ?
                <div className="histirucal3"></div> : null
            }
            {data.state === 3 ?
                <div className="histirucal-state0">
                    
                </div> : null
            }
            <div className="histirucal-box">
                {data.state === 1 ?
                    <p>
                        
                        <br/>
                        <br/>
                    </p> :
                    data.state === 2 ?
                        <p>
                            
                            <br/>
                            <br/>
                        </p> :
                        data.state === 4 ?
                            <p className="state4">
                                
                                <br/>
                            </p> :
                            data.state === 5 ?
                                <p className="state4">
                                    
                                    <br/>
                                    <br/>
                                </p> : null
                }
                {data.state === 1 ?
                    <h2></h2> :
                    data.state === 4 ?
                        <div className="mobile_phone">
                            <span></span>
                            <span>{data.mobile}</span>
                        </div> : null
                }
                {data.state === 1 ?
                    <ul>
                        {
                            data.dataList.map((item, index) => {
                                return <li key={index}>
                                    <h4>{item.type_name}</h4>
                                    <h3>{item.title}</h3>
                                    <span>{item.booking_time}</span>
                                </li>
                            })
                        }
                    </ul> :
                    data.state === 4 ?
                        <div className="mobile_phone">
                            <span></span>
                            <span onClick={getCode} className="testing_ck">{time.testing_txt}</span>
                            <input
                                type="text"
                                id="mobile-testing"
                                className="testing"
                                maxLength="6"
                                onChange={(e) => {
                                    setData({...data, testing: e.target.value})
                                }}
                            />
                        </div> : null
                }
            </div>
            {data.state === 4 ?
                <div className="histirucal-state0"
                >
                </div> : null
            }
            {
                data.state !== 6 ?
                    <div className="fixbtn-box">
                        <button className="bttn btn-yes" onClick={btnClick}>{data.btnYes}</button>
                        {data.btnboolean ?
                            <button className="bttn btn-no">{data.btnNo}</button> : null
                        }
                    </div> : null
            }
            {
                data.state === 6 ?
                    <div className="loading">
                        <img className="icon" src={loading_gif} alt={''}/>
                        <p className="tip"></p>
                    </div> : null
            }
        </div>
    );
}

export default Historical;
